:root {
  --bodyBg: #1D1D1D;
  --accent: #E3B748;
  --hoverColor: #E2BE68;
  --mainPadding: 30px;
  --mainMargin: 100px;
  --transition: all .2s ease;
}

.reviews__item {
  padding: 36px 26px 38px;
  background: #1D1D1D;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 14px;
}

.nav__item a {
  position: relative;
}
.nav__item a::after {
  content: "";
  position: absolute;
  left: auto;
  right: 0;
  bottom: 0;
  width: 0;
  height: 1px;
  background: #fff;
  transition: var(--transition);
}
.nav__item a:hover::after, .nav__item a:focus::after {
  right: auto;
  left: 0;
  width: 100%;
}

@media screen and (max-width: 767px) {
  :root {
    --mainPadding: 20px;
    --mainMargin: 100px;
  }
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.content-box-component {
  box-sizing: content-box;
}

body {
  display: flex;
  flex-direction: column;
  background: var(--bodyBg);
  color: #fff;
  font-family: "Inter", "Helvetica", Arial, sans-serif;
  width: 100%;
  height: 100%;
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  padding: 0;
  cursor: pointer;
  background: transparent;
  border: none;
  color: inherit;
}

ul, ol {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

p {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

picture {
  display: block;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.background-image {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow: hidden;
}
.background-image picture {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}
.background-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.wrapper {
  width: 100%;
  max-width: 100%;
  min-width: 320px;
  margin: 0 auto;
}

.swiper-button-prev, .swiper-button-next {
  width: 91px;
  height: 91px;
  border-radius: 50%;
  background: #E6E6E6;
  margin: 0;
  position: relative;
  transition: var(--transition);
}
.swiper-button-prev:hover, .swiper-button-prev:focus, .swiper-button-next:hover, .swiper-button-next:focus {
  background: var(--accent);
}
.swiper-button-prev::after, .swiper-button-next::after {
  content: "";
  width: 39px;
  height: 39px;
  position: absolute;
  margin: auto;
}
.swiper-button-prev::after {
  background: url("../images/icons/prev.svg") no-repeat center/contain;
}
.swiper-button-next::after {
  background: url("../images/icons/next.svg") no-repeat center/contain;
}
.swiper-pagination {
  top: auto !important;
  bottom: 0 !important;
}
.swiper-pagination-bullet {
  width: 14px;
  height: 14px;
  margin: 0 6px !important;
  background: #ccc;
  opacity: 1;
  transform: scale(0.8);
  transition: all 0.3s ease;
}
.swiper-pagination-bullet-active {
  background: var(--accent);
  transform: scale(1);
}

@media screen and (max-width: 1200px) {
  .swiper-button-prev, .swiper-button-next {
    width: 80px;
    height: 80px;
  }
}
@media screen and (max-width: 767px) {
  .swiper-button-prev, .swiper-button-next {
    width: 70px;
    height: 70px;
  }
  .swiper-button-prev::after, .swiper-button-next::after {
    width: 30px;
    height: 30px;
  }
}
.container {
  width: 100%;
  max-width: 1200px;
  padding: 0 var(--mainPadding);
  margin: 0 auto;
}

.logo {
  display: inline-block;
  width: 143px;
}
.logo img {
  display: block;
  width: 100%;
}

.text {
  color: rgba(255, 255, 255, 0.3);
  font-weight: 500;
  font-size: 18px;
  line-height: 163%;
}

.btn {
  padding: 10px 24px;
  display: inline-block;
  background: var(--accent);
  color: #000;
  border-radius: 24px;
  text-align: center;
  font-size: 14px;
  line-height: 163%;
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition);
}
.btn:hover, .btn:focus {
  background: var(--hoverColor);
}

.email-form__box {
  width: 438px;
  position: relative;
}
.email-form__field {
  position: relative;
}
.email-form__icon {
  position: absolute;
  width: 22px;
  height: 22px;
  left: 28px;
  top: 0;
  bottom: 0;
  margin: auto;
  opacity: 0.1;
  transition: var(--transition);
}
.email-form__inp {
  width: 100%;
  padding: 24px 62px;
  padding-right: 180px;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 43px;
  font-weight: 500;
  font-size: 16px;
  line-height: 162.52%;
  color: #fff;
  border: none;
}
.email-form__inp:focus ~ .email-form__icon {
  opacity: 1;
}
.email-form .btn {
  padding: 18px 32px;
  position: absolute;
  right: 6px;
  top: 6px;
  bottom: 6px;
  border-radius: 43px;
  font-size: 16px;
}

.title {
  font-weight: 600;
  font-size: 54px;
  line-height: 162%;
  text-align: center;
}
.title.anim-item {
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}
.title.anim-item.visible {
  opacity: 1;
  transform: translateY(0);
}

.sec-title {
  font-weight: 600;
  font-size: 24px;
  line-height: 120%;
}

@media screen and (max-width: 1200px) {
  .title {
    font-size: 48px;
  }
  .sec-title {
    font-size: 22px;
    line-height: 140%;
  }
  .text {
    font-size: 16px;
  }
}
@media screen and (max-width: 900px) {
  .title {
    font-size: 44px;
  }
  .sec-title {
    font-size: 20px;
  }
}
@media screen and (max-width: 767px) {
  .title {
    font-size: 40px;
  }
  .sec-title {
    font-size: 18px;
  }
}
@media screen and (max-width: 500px) {
  .title {
    font-size: 36px;
  }
  .email-form__field {
    margin-bottom: 10px;
  }
  .email-form__inp {
    padding: 20px 62px;
  }
  .email-form .btn {
    width: 100%;
    position: static;
  }
}
@media screen and (max-width: 400px) {
  .title {
    font-size: 32px;
  }
}
.nav__list {
  display: flex;
  align-items: center;
  font-weight: 600;
  font-size: 16px;
  line-height: 160%;
}
.nav__item {
  margin-right: 40px;
}
.nav__item:last-child {
  margin-right: 0;
}
.header {
  padding-top: 230px;
  margin-bottom: 172px;
  padding-bottom: 192px;
  background: #111111;
}
.header__top {
  margin-bottom: 80px;
  padding-top: 30px;
  padding-bottom: 30px;
  background: #111111;
  z-index: 1000;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}
.header__top-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__tel {
  padding: 14px 35px;
  font-weight: 600;
  font-size: 16px;
  text-align: center;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  border: 2px solid #fff;
  transition: var(--transition);
  position: relative;
  z-index: 105;
}
.header__tel-icon {
  display: none;
}
.header__tel:hover, .header__tel:focus {
  background: #fff;
  color: #000;
}
.header__content {
  display: flex;
  align-items: center;
}
.header__info {
  width: calc(100% - 468px);
  padding-right: 92px;
  opacity: 0;
  transform: translateX(-40px);
  transition: all 0.6s ease-out;
}
.header__info.visible {
  opacity: 1;
  transform: translateX(0);
}
.header__title {
  margin-bottom: 36px;
  font-weight: 500;
  font-size: 66px;
  line-height: 148%;
}
.header__title span {
  font-weight: 700;
}
.header__text {
  margin-bottom: 48px;
}
.header__img {
  width: 468px;
  height: 468px;
  margin-top: 20px;
  border-radius: 55px;
  background: var(--accent);
  position: relative;
  background: url("../images/header/pattern.png") no-repeat center/contain;
  opacity: 0;
  transform: translateX(40px);
  transition: all 0.6s ease-out;
}
.header__img.visible {
  opacity: 1;
  transform: translateX(0);
}
.header__img-person {
  width: 444px;
  max-width: none;
  position: absolute;
  bottom: -20px;
}
.header__menu {
  margin-left: 30px;
}
.header__logo {
  position: relative;
  z-index: 105;
}

@media screen and (max-width: 1600px) {
  .header__top {
    padding: 30px 0;
  }
}
@media screen and (max-width: 1200px) {
  .header {
    padding-top: 180px;
    margin-bottom: var(--mainMargin);
  }
  .header__menu {
    margin-left: 0;
  }
  .header__top {
    padding: 20px 0;
  }
  .header__top-inner {
    position: relative;
  }
  .header__menu {
    order: -1;
  }
  .header__burger {
    width: 50px;
    height: 50px;
    padding: 10px;
    display: flex;
    position: relative;
    z-index: 110;
    justify-content: center;
    align-items: center;
  }
  .header__burger span {
    display: block;
    width: 100%;
    height: 2px;
    background: #fff;
    transition: all 0.4s ease;
    position: relative;
  }
  .header__burger span::before, .header__burger span::after {
    content: "";
    position: absolute;
    z-index: 1;
    top: -10px;
    width: 100%;
    height: 2px;
    background: inherit;
  }
  .header__burger span::after {
    top: auto;
    bottom: -10px;
  }
  .header__burger--active span {
    transform: rotate(135deg);
  }
  .header__burger--active span::before, .header__burger--active span::after {
    transform: rotate(90deg);
  }
  .header__burger--active span::before {
    top: 0;
  }
  .header__burger--active span::after {
    bottom: 0;
  }
  .header__nav {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all 0.4s ease;
    border-radius: 50%;
    transform: scale(0);
  }
  .header__nav--active {
    transform: scale(1);
    border-radius: 0;
  }
  .header__nav .nav__list {
    flex-direction: column;
    font-size: 24px;
  }
  .header__nav .nav__item {
    margin-right: 0;
    margin-bottom: 20px;
  }
  .header__logo {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
  .header__title {
    font-size: 56px;
  }
  .header__info {
    width: calc(100% - 400px);
  }
  .header__img {
    width: 400px;
    height: 400px;
  }
  .header__img-person {
    width: 380px;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media screen and (max-width: 1000px) {
  .header {
    padding-bottom: 100px;
  }
  .header__content {
    flex-direction: column;
    align-items: flex-start;
  }
  .header__info {
    width: 100%;
    padding-right: 0;
    transform: translateY(40px);
  }
  .header__info.visible {
    transform: translateY(0);
  }
  .header__img {
    width: 330px;
    height: 330px;
    margin-bottom: 30px;
    order: -1;
    transform: translateY(40px);
  }
  .header__img.visible {
    transform: translateY(0);
  }
  .header__img-person {
    width: 300px;
    bottom: -10px;
  }
  .header__title {
    margin-bottom: 20px;
    font-size: 44px;
  }
  .header__text {
    margin-bottom: 30px;
  }
  .header .email-form__box {
    width: 100%;
  }
}
@media screen and (max-width: 767px) {
  .header {
    padding-top: 150px;
  }
  .header__tel {
    width: 50px;
    height: 50px;
    padding: 10px;
    font-size: 0;
    border: none;
  }
  .header__tel-icon {
    display: block;
  }
  .header__tel:hover, .header__tel:focus {
    background: transparent;
  }
}
@media screen and (max-width: 400px) {
  .header {
    padding-top: 100px;
    padding-bottom: 80px;
  }
  .header__top {
    margin-bottom: 60px;
  }
  .header__title {
    font-size: 36px;
  }
  .header__img {
    width: 100%;
    max-width: 360px;
    height: 360px;
  }
  .header__img-person {
    width: 280px;
  }
}
.main {
  flex-grow: 1;
}

.introduce {
  margin-bottom: 170px;
}
.introduce__inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.introduce__left {
  padding-right: 87px;
  border-right: 1px solid #6A6A6A;
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}
.introduce__left.visible {
  opacity: 1;
  transform: translateY(0);
}
.introduce__right {
  height: auto;
  padding-left: 87px;
  opacity: 0;
  transform: translateY(40px);
  transition: all 0.6s ease-out;
}
.introduce__right.visible {
  opacity: 1;
  transform: translateY(0);
}
.introduce__left, .introduce__right {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.introduce .title {
  text-align: left;
}
.introduce__name {
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 32px;
  line-height: 163%;
}

@media screen and (max-width: 1200px) {
  .introduce {
    margin-bottom: var(--mainMargin);
  }
  .introduce__inner {
    grid-template-columns: 1fr;
  }
  .introduce__left {
    margin-bottom: 20px;
    padding-bottom: 10px;
    padding-right: 0;
    border-right: 0;
    border-bottom: 1px solid #6A6A6A;
  }
  .introduce__right {
    padding-left: 0;
  }
}
.features {
  margin-bottom: 179px;
}
.features__title {
  margin-bottom: 97px;
}
.features__slider {
  overflow: hidden;
  position: relative;
}
.features__list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-gap: 31px;
}
.features__item {
  padding: 28px 24px 36px;
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 14px;
  transition: var(--transition);
  opacity: 0;
  transform: translateX(-40px);
  transition: all 0.6s ease-out;
}
.features__item.visible {
  opacity: 1;
  transform: translateX(0);
}
.features__item:hover, .features__item:focus {
  background: #2E2E2E;
}
